import {useEffect, useState} from "react";
import {
    AppBar,
    Card,
    Container,
    createTheme,
    Divider,
    IconButton,
    ThemeProvider, Toolbar,
    Menu,
    Typography,
    Avatar, MenuItem, Box
} from "@mui/material";
import {DarkMode, LightMode, Translate} from "@mui/icons-material";
import LngSupport from './Lng/LngSupport.js';
import Lng from './Lng/Lng.js';
import myPhoto from './assets/static/img/my_photo.jpg'

function App() {

    const [darkTheme, setDarkTheme] = useState(true)
    const [translateAnchor, setTranslateAnchor] = useState(null);
    const [userTranslate, setUserTranslate] = useState('')
    const translateDefault = 'en'
    const [translate, setTranslate] = useState(Lng[translateDefault])
    const [translateMenu, setTranslateMenu] = useState([])
    const translateOpen = Boolean(translateAnchor);
    // const [translate, setTranslate] = useState(false)
    const theme = createTheme({
        palette: {
            mode: darkTheme ? 'dark' : 'light'
        },
    })

    const lngDetect = () => {
        if (!userTranslate) {
            const detectLanguage = navigator.language.substr(0, 2) || navigator.userLanguage.substr(0, 2);
            if (LngSupport.hasOwnProperty(detectLanguage)) {
                if (LngSupport[detectLanguage].autouse) {
                    setUserTranslate(detectLanguage)
                }
            }
        }
    };

    const lngLoad = () => {
        if (Lng.hasOwnProperty(translateDefault)) {
            if (Lng.hasOwnProperty(userTranslate)) {
                const obj = Object.assign({}, Lng[translateDefault], Lng[userTranslate])
                document.documentElement.lang = userTranslate;
                setTranslate(obj)
            } else {console.log("Main-lngLoad()", " : '"+translateDefault+" (LngCurrent)' not found in Lng.js!")};
        } else {console.log("Main-lngLoad()", " : '"+translateDefault+" (LngDefault)' not found in Lng.js!")};
    };

    lngDetect()

    useEffect(() => {
        if (translateMenu.length === 0 ) {
            const LngOptions = [];
            Object.entries(LngSupport).forEach((el) => {
                LngOptions.push(<MenuItem key={el[0]} disabled={el[1].disabled}
                                          onClick={() => handleTranslateOnClick(el[0])}>{el[1].name}</MenuItem>)
            })
            setTranslateMenu(LngOptions)
        }
        lngLoad()
    }, // eslint-disable-next-line
        [userTranslate])


    const handleThemeChange = () => {
        setDarkTheme(!darkTheme)
    };

    const handleTranslateMenuOpen = (event) => {
        setTranslateAnchor(event.currentTarget)
    };

    const handleTranslateClose = () => {
        setTranslateAnchor(null);
    };

    const handleTranslateOnClick = (lng) => {
        setUserTranslate(lng)
        setTranslateAnchor(null);
    };


    return (
        <Container maxWidth="xl">
            <ThemeProvider theme={theme}>
                <AppBar position="static" color="default">
                    <Toolbar>
                        <Typography variant="h6">
                            octanium91
                        </Typography>
                        <div style={{marginLeft: 'auto'}}>
                            <IconButton
                                id="translate-menu"
                                aria-controls="basic-menu"
                                aria-haspopup="true"
                                aria-expanded={translateOpen ? 'true' : undefined}
                                onClick={handleTranslateMenuOpen}
                            >
                                <Translate/>
                            </IconButton>
                            <Menu
                                id="translate-menu"
                                anchorEl={translateAnchor}
                                open={translateOpen}
                                onClose={handleTranslateClose}
                                MenuListProps={{
                                    'aria-labelledby': 'basic-button',
                                }}
                            >
                                {translateMenu}
                            </Menu>
                            <IconButton onClick={handleThemeChange}>
                                {darkTheme ? <LightMode/> : <DarkMode/>}
                            </IconButton>
                        </div>
                    </Toolbar>
                </AppBar>
                <Divider/>
                <Card>
                    <Box sx={{p: 2}} display="block" alignContent={'center'} alignItems={'center'}>
                        <Box>
                            <Avatar
                                style={{margin: "auto"}}
                                alt="Remy Sharp"
                                src={myPhoto}
                                sx={{width: 256, height: 256}}
                            />
                        </Box>
                        <Divider/>
                        <Box>
                            <Typography gutterBottom variant="h4" component="div" align="center">
                                {translate?.AboutMeHelloText}
                            </Typography>
                            <Typography variant="h5" color="text.secondary" align="center">
                                {translate?.AboutMeText}
                            </Typography>
                        </Box>
                    </Box>
                </Card>
            </ThemeProvider>
        </Container>
    );
}

export default App;
